<template>
  <div id="app">
    <textarea v-model="text" id="box"></textarea>
    <template v-for="item in data">
      <estnltk v-bind:data="item"/>
    </template>
  </div>

</template>

<script>
  import Vue from 'vue'

  import Estnltk from './components/Estnltk'

  export default {
    name: 'App',
    components: {
      Estnltk
    },
    data: function () {
      return {
        text: 'Siia kirjutatud teksti anal端端sib Estnltk.',
        updateTime: 0,
        data: []
      }
    },
    created(){
      this.update()
    },
    watch: {
      text: function () {
        if (this.text.endsWith(' ') || (Date.now() - this.updateTime >= 2) ) {
          this.updateTime = Date.now()
          this.update()
        }
      }
    },
    methods: {
      update(){
        var request = new XMLHttpRequest();
        request.open('POST', 'http://estnltk-demo.keeleressursid.ee/estnltk', true);
        request.setRequestHeader('Content-Type', 'application/json; charset=UTF-8');
        request.send(
          JSON.stringify({
            text: this.text
          }));

        var vm = this
        request.onload = function () {
          Vue.set(vm.data, 0, JSON.parse(this.response))
        }
      }
    }
  }
</script>


<style>
  #box{
    height:100px;
    width:100%;
  }
  #app {
  }
</style>



