<template>
    <div class="estnltk"></div>
</template>

<script type="text/ecmascript-6">
    import Vue from 'vue'
    import _ from 'lodash'

    import rangy from 'rangy';
    import rangyClassApplier from 'rangy/lib/rangy-classapplier';
    import rangyTextRange from 'rangy/lib/rangy-textrange';

    function sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms)
        );
    }


    async function highlightCharacterRange(el, start, end, classname, element_name, data) {
        while (!rangy.initialized) {
            await
                    sleep(1)
        }
        var range = rangy.createRange();
        range.moveToBookmark({
            containerNode: el[0],
            start: start,
            end: end
        });

        range.splitBoundaries();
        var textNodes = range.getNodes([3])

        var c = 0;
        var len = textNodes.length
        var spanelem;
        for (var textNode of textNodes) {
            spanelem = document.createElement(element_name);
            spanelem.className = classname;
            if (c == 0) {
                spanelem.classList.add('first')
                //first part hold canonical data
                for (var key of Object.keys(data)) {
                    spanelem.dataset[key] = data[key]
                }
            }

            textNode.parentNode.insertBefore(spanelem, textNode);
            spanelem.appendChild(textNode);
        }
        spanelem.classList.add('last')
    }
    ;

    function extracted(vm, parent) {
        var parent = vm.$el;
        while (parent.firstChild) {
            parent.removeChild(parent.firstChild);
        }

        parent.innerHTML = '';
        parent.innerHTML = vm.data['text'];


        Vue.nextTick(function () {
//                            for (var key in vm.data) {
                    var keys = ['paragraphs',
                        'sentences',
                        'words',
                        'named_entities'];

                    for (var key of keys) {

                        if (key == 'text') {
                            //do nothing
                        } else {
                            for (var span of vm.data[key]) {
                                var start = span.start;
                                var end = span.end;
                                var data = {};

                                for (var k of Object.keys(span)) {
                                    if (k !== 'start' && k !== 'end') {
                                        if (typeof (span[k]) == 'string') {
                                            data[k] = span[k]
                                        } else {
                                            data[k] = JSON.stringify(span[k])
                                        }
                                    }
                                }
                                if (key == 'paragraphs') {
                                    highlightCharacterRange([parent], start, end, key, 'p', data);
                                } else {
                                    highlightCharacterRange([parent], start, end, key, 'span', data);
                                }
                            }
                        }
                    }
                }
        )
    }

    var _render = _.debounce(extracted, 200);

    export default {
        name: 'estnltk',
        props: ['data'],
        data: function () {
            return {}
        },
        mounted: function () {
            var vm = this;
            extracted(vm, parent);
        },
        watch: {
            data: function () {
                this.render()
            }
        },
        methods: {
            render: function () {
                var vm = this;
                _render(vm, parent);
            }
        }
    }

</script>
<style>
    .words {
        background-color: #ecf0f1;
        padding: 1px;
    }

    .words:hover {
        background-color: red;
    }

    /*.words.last {*/
        /*margin-left: 2px;*/
    /*}*/


    .sentences {
        background-color: #2c3e50;
        padding: 2px;
    }
    /*.paragraphs {*/
        /*padding: 3px;*/
        /*background-color: #bdc3c7;*/
        /*display: inline-block;*/
    /*}*/
</style>



